@function getExtensionAssetsPath($asset){
@return '../extensions/JJAcme/SpecialBadge/2.0.0/' + $asset;
}

@function getThemeAssetsPath($asset){
@return '../extensions/J_J/JJ_Base_Theme/2.0.8/' + $asset;
}

